import { Button, Paper } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import KeyboardArrowLeft from "@material-ui/icons/KeyboardArrowLeft";
import KeyboardArrowRight from "@material-ui/icons/KeyboardArrowRight";

interface TestProgressProps {
    active: number;
    count: number;
    prev: () => void;
    next: () => void;
    goTo: (pageNumber: number) => void;
}
let timeoutId: number | null = null;

const TestProgress = (props: TestProgressProps) => {
    const [inputValue, setInputValue] = useState<number>(props.active + 1);

    useEffect(() => setInputValue(props.active + 1), [props.active]);
    return (
        <Paper elevation={3}>
            <div className="stepper">
                <Button size="small" onClick={props.prev} disabled={props.active === 0}>
                    <KeyboardArrowLeft />
                    Back
                </Button>
                <div>
                    <input
                        type="number"
                        min={1}
                        max={props.count}
                        step={1}
                        value={inputValue}
                        onChange={(e) => {
                            let parsedValue = parseInt(e.target.value);
                            if (parsedValue > props.count) parsedValue = props.count;
                            if (parsedValue < 1) parsedValue = 1;
                            console.log("cahnge", parsedValue);
                            if (timeoutId) {
                                console.log("canceled timeout");

                                window.clearTimeout();
                            }
                            if (!isNaN(parsedValue)) {
                                timeoutId = window.setTimeout(() => {
                                    timeoutId = null;
                                    console.log("timeout", parsedValue);
                                    props.goTo(parsedValue);
                                }, 1000);
                            }
                            setInputValue(parsedValue);
                        }}
                    />
                    {" / "}
                    {props.count}
                </div>
                <Button size="small" onClick={props.next} disabled={props.active >= props.count - 1}>
                    Next
                    <KeyboardArrowRight />
                </Button>
            </div>
        </Paper>
    );
};
export default TestProgress;
