import { Button, Paper } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import { ICurrentTest, IStats, QuestionState } from "../../interfaces/ICookies";
import { IQuestion } from "../../interfaces/IQuestion";
import { useLocalStorage } from "../../util/localStorage";
import Translation from "../../util/Translation";
import Question from "../question/Question";
import TestNavigation from "./TestNavigation";
import TestProgress from "./TestProgress";

interface TestParams {
    progress: number;
    test: IQuestion[];
    questionSet: string;
}

const Test = ({ progress, test, questionSet }: TestParams) => {
    const [currentTest, setCurrentTest] = useLocalStorage<ICurrentTest>("currentTest");
    const [stats, setStats] = useLocalStorage<IStats>("stats");

    const [currentAnswer, setCurrentAnswer] = useState<string>("");

    const history = useHistory();

    if (progress >= test.length) {
        console.log(progress, test.length);
        const index = currentTest?.givenAnswers.findIndex((item) => !item) ?? 1;
        if (index >= 0) {
            history.push("/trainer/" + (index + 1));
            window.setTimeout(() => {
                toast.info(<Translation>stillRemainingQuestions</Translation>);
            }, 1000);
        } else history.push("/result");
    }

    const onAnswerSubmitted = () => {
        const currentTestNew = currentTest ?? ({ questionOrder: [], usedOptions: {}, givenAnswers: [] } as any);
        if (isNaN(parseInt(currentAnswer))) {
            toast.warning(<Translation>noAnswerSelected</Translation>);
        } else if (!currentTestNew?.givenAnswers[progress]) {
            const question = test[progress];

            const state = question.correct === parseInt(currentAnswer) ? QuestionState.Correct : QuestionState.Wrong;

            const newStats = stats ?? {};
            newStats[questionSet] = stats?.[questionSet] ?? {};
            newStats[questionSet][question.id] = stats?.[questionSet][question.id] ?? {
                answerHistory: [],
                currentState: state,
            };

            newStats[questionSet][question.id].currentState = state;
            newStats[questionSet][question.id].answerHistory.push({
                result: state,
                time: new Date().getTime(),
            });
            setStats(newStats);
            currentTestNew.givenAnswers[progress] = {
                questionIndex: progress,
                given: currentAnswer,
                correct: question.correct,
            };
            setCurrentTest({ ...currentTestNew });
        }
    };
    useEffect(() => {
        if (currentTest?.givenAnswers[progress]) {
            setCurrentAnswer(currentTest.givenAnswers[progress].given);
        } else {
            setCurrentAnswer("");
        }
    }, [progress]);
    return (
        <div>
            <TestProgress
                count={test.length}
                active={progress}
                next={() => {
                    history.push("/trainer/" + (progress + 2));
                }}
                prev={() => {
                    history.push("/trainer/" + progress);
                }}
                goTo={(pageNumber: number) => {
                    history.push("/trainer/" + pageNumber);
                }}
            />
            {currentTest &&
                currentTest.givenAnswers.length === test.length &&
                currentTest.givenAnswers.findIndex((item) => !item?.given) < 0 && (
                    <div className="resultButton">
                        <Paper elevation={3}>
                            <Button variant="contained" color="primary" onClick={() => history.push("/result")}>
                                <Translation>goToResult</Translation>
                            </Button>
                        </Paper>
                    </div>
                )}
            {test[progress] && (
                <Question
                    key={progress}
                    {...test[progress]}
                    currentAnswer={currentAnswer}
                    setCurrentAnswer={setCurrentAnswer}
                    showCorrect={!!currentTest?.givenAnswers?.[progress]?.given}
                ></Question>
            )}
            <TestNavigation
                currentState={currentAnswer}
                checked={!!currentTest?.givenAnswers?.[progress]?.given}
                checkAnswers={onAnswerSubmitted}
                next={
                    progress + 1 < test.length
                        ? () => {
                              history.push("/trainer/" + (progress + 2));
                          }
                        : undefined
                }
            />
        </div>
    );
};

export default Test;
