import { Button, Paper } from "@material-ui/core";
import React from "react";
import { toast } from "react-toastify";
import Translation from "../../util/Translation";

interface TestNavigationProps {
    currentState?: string;
    checked: boolean;
    checkAnswers: () => void;
    next?: () => void;
}
const TestNavigation = (props: TestNavigationProps) => {
    if (props.checked && !props.next) return null;
    return (
        <Paper elevation={3}>
            <div className="questionNavi">
                {props.checked ? (
                    props.next && (
                        <Button
                            variant="contained"
                            color="primary"
                            onClick={() => {
                                props.next?.();
                            }}
                        >
                            <Translation>next</Translation>
                        </Button>
                    )
                ) : (
                    <div
                        onClick={() => {
                            if (!props.currentState) toast.warning("Du hast keine Antwort ausgewählt!");
                        }}
                    >
                        <Button
                            variant="contained"
                            color="primary"
                            onClick={() => props.checkAnswers()}
                            disabled={!props.currentState}
                        >
                            <Translation>check</Translation>
                        </Button>
                    </div>
                )}
            </div>
        </Paper>
    );
};
export default TestNavigation;
