import { Paper, Radio } from "@material-ui/core";
import React from "react";
import Translation from "../../util/Translation";
import ParsedText from "../ParsedText";

interface AnswerProps {
    answers: string[];
    correct: number;
    selected?: string;
    showCorrect?: boolean;
    setCurrentAnswer?: (value: string) => void;
}
const Answers = ({ answers, selected, correct, setCurrentAnswer, showCorrect }: AnswerProps) => {
    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        if (!showCorrect) setCurrentAnswer?.(event.target.value);
    };
    return (
        <Paper elevation={3}>
            <h2>
                <Translation>answers</Translation>
            </h2>
            {answers.map((answer, index) => (
                <div
                    className={
                        "answers " +
                        (showCorrect ? (correct === index ? "correct" : selected === String(index) ? "false" : "") : "")
                    }
                    key={index}
                >
                    <Radio
                        checked={selected === String(index)}
                        value={index}
                        onChange={handleChange}
                        disabled={showCorrect}
                    />

                    <ParsedText>{answer}</ParsedText>
                </div>
            ))}
        </Paper>
    );
};
export default Answers;
